.info-popup-button-container > div {
  display: inline-block;
}

a.popup-info-button {
  color: #3232c8;
  opacity: .8;
  min-height: 22px;
  background-position: 4px;
  background-repeat: no-repeat;
  background-size: 22px;
  border: 1px solid #3232c8;
  border-radius: 2px;
  margin-top: -5px;
  margin-left: 4px;
  padding: 4px 4px 4px 34px;
  display: inline-block;
}

a.popup-info-button > span {
  height: 22px;
  padding: 0;
  line-height: 22px;
  display: inline-block;
}

a.code-link-with-icon {
  min-height: 12px;
  background-position: 0%;
  background-repeat: no-repeat;
  background-size: 12px 12px;
  margin-right: 1rem;
  padding: 5px 5px 5px 17px;
}

a.popup-info-button:hover {
  opacity: 1;
  background-color: rgba(50, 50, 200, .1);
  box-shadow: 3px 3px 5px rgba(50, 50, 200, .1);
}

